import React from "react";
import Link from "gatsby-link";
import Lekcija from "../components/lekcija";
import URL from "../components/URL";
import groupBy from 'lodash/groupBy';
import toArray from 'lodash/toArray';
import styles from './cjeline.module.scss';



export default ({ data }) => {

// console.log(data.allMarkdownRemark.edges);
var groups = groupBy(data.allMarkdownRemark.edges,"node.frontmatter.lekcija");
const LekcijeSet = toArray(groups);

  return (
    <div className="container">
     <h2 className="title"> {data.allMarkdownRemark.edges[0].node.frontmatter.cjelina}</h2>
     <div className="breadcrumbs"><span><Link to="/">Naslovna</Link> </span><span>/ <Link to={`/${data.allMarkdownRemark.edges[0].node.frontmatter.razred}/`}>Matematika {data.allMarkdownRemark.edges[0].node.frontmatter.razred}</Link> </span></div>
      <div className="flex-list">
      
      {LekcijeSet.map((item,index)  =>
         <Lekcija data={item} key={index}>Lekcija</Lekcija>           
      )}
      </div>
    </div>
  )
}
export const query = graphql`
query CjelinaIndexQuery($cjelina: String, $razred: String) {
  allMarkdownRemark(sort: {order: ASC, fields: [frontmatter___title]}, filter: {frontmatter: {cjelina: {eq: $cjelina}
razred: {eq: $razred}}}) {
    edges {
      node {
        id
        frontmatter {
          razred
          title
          cjelina
          lekcija
          brojCjeline
        }
      }
    }
  }
}
`


