import React, { Component } from "react";
import Link from 'gatsby-link';
import URL from './URL';
import styles from "./header.module.scss";



class lekcija extends Component {

	render(){	

		var data = this.props.data;
		return <div className="accordion">
  					<div className="header"><b>{data[0].node.frontmatter.lekcija}</b></div>
  					{
  						data.map((item,index)=>
  							<URL key={index} to={`/${item.node.frontmatter.razred}/${item.node.frontmatter.cjelina}/${item.node.frontmatter.lekcija}/${item.node.frontmatter.title}/`}>{item.node.frontmatter.title}</URL>
  						)
  					}
  					 
				</div>
	}		
}

export default lekcija


